/**
 * Base: Buttons
 */
.button {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  background-color: red;
  white-space: nowrap;
  user-select: none;
  position: relative;
  font-weight: 400;
  font-family: Arial;
  transition: background 0.3s, color 0.3s, border-color 0.3s;
  padding: 10px 20px;
  color: white;
  border: 1px solid transparent;
  text-decoration: none;
}
.button:hover,
.button:focus {
  color: white;
  background: #cc0000;
  text-decoration: none;
  outline: none;
}
.button[disabled],
[disabled] .button {
  cursor: not-allowed;
}
.button--ghost {
  color: black;
  border: solid 1px red;
  background-color: transparent;
}
.button--ghost:hover,
.button--ghost:focus {
  color: white;
  background-color: red;
}
